<template>

  <div id="app">
    <h1>在线翻译</h1>
    <h5>简单 / 准确 / 易用</h5>
    <translateForm v-on:formSubmit="translateText"></translateForm>
    <translateOutput v-text="translatedText"></translateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/translateForm'
import TranslateOutput from './components/translateOutput'

export default {
  name: 'App',
  data: function(){
    return {
      translatedText: ''
    }
  },
  methods: {
    translateText: function(text, language) {
      // alert(text);
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180922T102617Z.ed4ae14c6da1acf9.2bfe7e37a0a91307173cdd3ab29f7064fdef9cb2&lang='+language+'&text='+text)
      .then((respone) => {
        // console.log(respone.body.text[0])
        this.translatedText = respone.body.text[0];
      })
    }
  },
  components: {
    TranslateForm, TranslateOutput
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 100px;
}
h5 {
  color: #cccccc;
}
</style>



