<template>
  <div id="translateForm">
    <div class="wrapper">
      <form v-on:submit = "formSubmit">
        <input class="input" type="text" v-model="textToTranslate" placeholder="请输入要翻译的内容">
        <select v-model="language" class="select">
          <option value="en">English</option>
          <option value="ru">Russian</option>
          <option value="ko">Korean</option>
          <option value="ja">Japanese</option>
        </select>
        <input class="btn" type="submit" value="翻译">
      </form>
    </div>
  </div>
</template>

<script>

export default {
  name: 'translateForm',
  data: function() {
    return {
      textToTranslate: '',
      language: ''
    }
  },
  methods: {
    formSubmit: function(e) {
      // alert(this.textToTranslate);
      this.$emit("formSubmit", this.textToTranslate,this.language);
      e.preventDefault();
    }
  },
  created: function(){
    this.language = "en"
  }
}
</script>

<style>

.wrapper {
  margin: 0 auto;
  width: 500px;
  padding: 20px;
  background-color: #f00;
  border-radius: 10px;
  box-shadow: 0 10px 12px rgb(105, 53, 53);
}
.btn {
  background-color:#1AAD19;
  border: none;
  color: #ffffff;
  height: 30px;
  width: 60px
}
.btn:hover {
  background-color:rgb(151, 91, 207);;
}
.input {
  padding: 0 0 0 10px;
  border: none;
  height: 30px;
  width: 300px;
}
.select {
  padding: 0 10px;
  border: none;
  height: 30px;
  width: 100px;
}
@media screen and (max-width: 414px) {
  .btn{width: 100%}
  .select{width: 100%}
  .input{width: 97%; display: block;}
  .wrapper{width: 305px;}
}
</style>



